body {
  background-color: white;
}

.app-wrapper {
  margin-left: 120px;
  margin-top: 30px;
  margin-bottom: 20px;
  display: grid;
  width: 1200px;

  grid-template-areas: 
  "h h"
  "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background-color: #172D13;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 5px;
  padding: 10px;
}
